      -webkit-justify-content: center;
              justify-content: center;
      -webkit-align-items: center;
              align-items: center;
      height: 100%;
      -webkit-flex-flow: wrap;
              flex-flow: wrap;
  }
  .App {
    max-width: 600px;
    width: 100%;
